import React, { useState, useEffect } from 'react';
import axios from 'axios';
import io from 'socket.io-client';
const socket = io('http://localhost:3009');
function Countdown() {
  

  const [secondsToAdd] = useState(45); // Number of seconds to add to current time
 
  const [endTime, setEndTime] = useState(null); // Initialize endTime as null
  const [currentTime, setCurrentTime] = useState(new Date());
  const [remainingSeconds, setRemainingSeconds] = useState(secondsToAdd);
  const [result, setResult] = useState(0);

  useEffect(() => {
    // Listen for the 'endTimeUpdated' event from the server
    socket.on('endTimeUpdated', (data) => {
      console.log(data);
      setEndTime(new Date(data.endTime).getTime());
    });

    return () => {
      socket.disconnect();
    };
  }, []);
  useEffect(() => {
    
    const fetchEndTime = async () => {
      try {
        const response = await axios.get('http://localhost:3009/api/getEndTime'); // Replace with your API endpoint
        const fetchedEndTime = response.data.endTime;
        if (fetchedEndTime) {
          setEndTime(new Date(Number(fetchedEndTime)).getTime());
          
        }
      } catch (error) {
        console.error('Error fetching endTime:', error);
      }
    };

    fetchEndTime();
  }, []);

 
  useEffect(() => {
    if (endTime) {
      const interval = setInterval(() => {
        const now = new Date();
        setCurrentTime(now);

        const remainingTime = Math.floor((endTime - now.getTime()) / 1000);

        if (remainingTime <= 0) {
          clearInterval(interval); // Clear interval when remaining time reaches zero
          setRemainingSeconds(0); // Set remaining seconds to zero
        } else {
          setRemainingSeconds(remainingTime);
          setResult(Math.ceil(remainingTime / 15)); // Calculate and set the result using Math.ceil
        }
      }, 1000); // Update every 1 second

      return () => clearInterval(interval); // Clean up the interval when component unmounts
    }
  }, [endTime]);

  const handleSaveEndTime = async () => {
    const dbSave = new Date().getTime() + secondsToAdd * 1000
    try {
      await axios.post('http://localhost:3009/api/saveEndTime', { dbSave }); // Replace with your API endpoint and data
      console.log('EndTime saved successfully');
    } catch (error) {
      console.error('Error saving endTime:', error);
    }
  };


  




  const countdownDisplay = Array.from({ length: remainingSeconds }, (_, index) => (remainingSeconds - index) % 15 || 15);
  const firstCountdownResult = countdownDisplay[0];

  return (
    <div>

      <p>Time to Add (in seconds): {secondsToAdd}</p>
      <p>End Time: {endTime ? new Date(endTime).toLocaleTimeString() : 'Fetching...'}</p>
      <p>Current Time: {currentTime.toLocaleTimeString()}</p>
      {remainingSeconds >= 0 && (
        <>
          <p>First Countdown Result: {firstCountdownResult}</p>
          <p>Result of Remaining Seconds / 15: {result}</p>
          <button onClick={handleSaveEndTime}>Save EndTime</button>
        </>
      )}
      {remainingSeconds === 0 && (
        <p>Countdown has reached the end time.</p>
      )}
    </div>
  );
}

export default Countdown;
